export const deleteEmail = async (
    accessToken: string,
    graphMsgId: string,
    callback: (error: Error | null, data: any | null) => void
): Promise<void> => {
    try {
        const response = await fetch(`https://graph.microsoft.com/v1.0/me/messages/${graphMsgId}`, {
            method: "DELETE",
            headers: {
                Authorization: `Bearer ${accessToken}`,
            },
        });

        if (!response.ok) {
            const error = new Error("Failed to delete email");
            callback(error, null);
            return;
        }

        callback(null, { success: true });
    } catch (error) {
        console.error("Error deleting email:", error);
        callback(error as Error, null);
    }
};