import React, { useState, useEffect } from 'react';
import { Typography, Box, Button, Card, CardContent } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import UIText from '../../../utilities/textResource';
import { CompanyConfig, DEFAULT_CONFIG, PhishBaitValues } from '../../../utilities/types';
import { notyf } from '../../../hooks/useToast';
import { deleteEmail } from '../../../services/deleteEmail';

// Extend Window interface
declare global {
    interface Window {
        headerExtractor: {
            extractPhishBaitValues: (headers: string) => Promise<PhishBaitValues>;
            getEmailHeaders: (item: Office.MailboxItem) => Promise<string | null>;
        };
        Office: {
            context: Office.Context;
            MailboxEnums: typeof Office.MailboxEnums;
            AsyncResultStatus: typeof Office.AsyncResultStatus;
        };
    }
}

// Add Office.js type declarations
declare namespace Office {
    namespace MailboxEnums {
        enum DisplayMode {
            NewWindow = "newWindow"
        }
        enum AttachmentType {
            Item = "item"
        }
        enum RestVersion {
            v2_0 = "v2.0"
        }
        enum MoveType {
            Deleted = "deleted"
        }
    }
    enum CoercionType {
        Text = "text",
        Html = "html"
    }
    enum AsyncResultStatus {
        Succeeded = "succeeded",
        Failed = "failed"
    }
    interface AsyncResult<T> {
        status: AsyncResultStatus;
        value?: T;
        error?: {
            name: string;
            message: string;
        };
    }
    interface MailboxItem {
        itemId?: string;
        subject?: string;
        sender?: {
            emailAddress: string;
        };
        getAllInternetHeadersAsync?: (callback: (result: AsyncResult<string>) => void) => void;
        getHeadersAsync?: (callback: (result: AsyncResult<string>) => void) => void;
        headers?: string;
        forward?: (options: any, callback: (result: AsyncResult<any>) => void) => void;
        body?: {
            contentType: string;
            content: string;
        };
        getAsync?: (coercionType: CoercionType, options: any, callback: (result: AsyncResult<string>) => void) => void;
    }
    interface Mailbox {
        item: MailboxItem;
        userProfile: {
            emailAddress: string;
        };
        diagnostics: {
            hostName: string;
        };
        displayNewMessageForm: (options: any, callback: (result: AsyncResult<any>) => void) => void;
        convertToRestId: (itemId: string, version: MailboxEnums.RestVersion) => string;
    }
    interface Context {
        mailbox: Mailbox;
    }
}

const Phishing: React.FC = () => {
    const navigate = useNavigate();

    const item = window.Office.context.mailbox.item;
    const DateAndTime = window.Office.context.mailbox.item.dateTimeCreated;
    const formattedDateTime = new Date(DateAndTime).toLocaleString('en-US', {
        month: 'numeric',
        day: 'numeric',
        year: 'numeric',
        hour: 'numeric',
        minute: '2-digit',
        second: '2-digit',
        hour12: true,
    });

    const DEBUG_MODE = false;

    // Environment detection
    const checkOutlookEnvironment = () => {
        return window.Office.context.mailbox.diagnostics.hostName === 'OutlookWebApp';
    }

    useEffect(() => {
        const userEmail = window.Office.context.mailbox.userProfile.emailAddress;
        const domain = userEmail.substring(userEmail.lastIndexOf("@") + 1);
        console.log(domain);
    }, []);

    const handleReportAsPhishing = async () => {
        console.log("Report as Phishing");
        navigate("/phishing-page");
        await reportPhishing();
    }

    const reportPhishing = async () => {
        const item = window.Office.context.mailbox.item;
        if (!item) {
            notyf.error("Cannot access email information");
            return;
        }

        try {
            const userEmail = window.Office.context.mailbox.userProfile.emailAddress;
            const domain = userEmail.substring(userEmail.lastIndexOf("@") + 1);
            const config = await getCompanyConfig(domain);
            const isNewOutlook = checkOutlookEnvironment();

            const headers = await getEmailHeaders(item);
            const isSimulation = headers?.includes("X-PHISHBAIT");
            // const isSimulation = true;
            console.log("isSimulation", isSimulation);

            if (isSimulation) {
                await sendReportToServer(item, userEmail, config, headers);
            } else {
                await handleEmailForwarding(item, userEmail, config, false, isNewOutlook);
            }
        } catch (error) {
            notyf.error(error);
        }
    }

    // Header Extractor functionality
    const extractPhishBaitValues = async (headers: string): Promise<PhishBaitValues> => {
        if (DEBUG_MODE) {
            console.log("Attempting to extract PhishBait values from headers");
            console.log("Headers received:", headers);
        }

        if (!headers) {
            if (DEBUG_MODE) console.log("No headers provided");
            return {
                campaignId: null,
                templateId: null,
                userId: null
            };
        }

        try {
            const campaignMatch = headers.match(/X-PHISHBAIT4:\s*(\d+)/i);
            const templateMatch = headers.match(/X-PHISHBAIT2:\s*(\d+)/i);
            const userMatch = headers.match(/X-PHISHBAIT3:\s*(\d+)/i);

            if (DEBUG_MODE) {
                console.log("Regex match results:", {
                    campaign: campaignMatch,
                    template: templateMatch,
                    user: userMatch
                });
            }

            const result = {
                campaignId: campaignMatch && campaignMatch[1] ? campaignMatch[1] : null,
                templateId: templateMatch && templateMatch[1] ? templateMatch[1] : null,
                userId: userMatch && userMatch[1] ? userMatch[1] : null
            };

            if (DEBUG_MODE) console.log("Found values:", result);
            return result;
        } catch (error) {
            if (DEBUG_MODE) console.error("Error extracting PhishBait values:", error);
            return {
                campaignId: null,
                templateId: null,
                userId: null
            };
        }
    }

    async function getEmailHeaders(item: Office.MailboxItem) {
        if (DEBUG_MODE) {
            console.log("Starting getEmailHeaders function");
            console.log("Item object:", item);
        }

        try {
            let headers = null;

            if (typeof item.getAllInternetHeadersAsync === 'function') {
                if (DEBUG_MODE) console.log("Using getAllInternetHeadersAsync");
                const result = await new Promise<Office.AsyncResult<string>>(resolve => item.getAllInternetHeadersAsync(resolve));
                if (result.status === Office.AsyncResultStatus.Succeeded && result.value) {
                    headers = result.value;
                }
            }

            if (!headers && typeof item.getHeadersAsync === 'function') {
                if (DEBUG_MODE) console.log("Using getHeadersAsync");
                const result = await new Promise<Office.AsyncResult<string>>(resolve => item.getHeadersAsync(resolve));
                if (result.status === Office.AsyncResultStatus.Succeeded && result.value) {
                    headers = result.value;
                }
            }

            if (!headers) {
                if (DEBUG_MODE) console.log("Using item.headers");
                headers = item.headers || null;
            }

            if (DEBUG_MODE) console.log("Final headers value:", headers);
            return headers;
        } catch (error) {
            if (DEBUG_MODE) console.error("Error in getEmailHeaders:", error);
            return null;
        }
    }

    // Initialize the global headerExtractor object
    if (DEBUG_MODE) console.log("Initializing headerExtractor functions");
    window.headerExtractor = {
        extractPhishBaitValues,
        getEmailHeaders
    };
    if (DEBUG_MODE) console.log("headerExtractor initialized with functions:", Object.keys(window.headerExtractor));

    // Optimized company config
    const getCompanyConfig = async (domain: string): Promise<CompanyConfig> => {
        try {
            const response = await fetch(`https://baitandphish.com/API/getConfigByDomain.php?domain=${encodeURIComponent(domain)}`);

            if (!response.ok) throw new Error("Failed to fetch config");

            const data = await response.json();
            if (!data.success || !data.config) throw new Error("Invalid config format");

            return {
                ...DEFAULT_CONFIG,
                ...data.config,
                OtherPhishingTargetEmails: data.config.OtherPhishingTargetEmails || `security@${domain}`
            };
        } catch (error) {
            if (DEBUG_MODE) console.error("Config error:", error);
            return {
                ...DEFAULT_CONFIG,
                OtherPhishingTargetEmails: `security@${domain}`
            };
        }
    }

    // Optimized server communication
    const sendReportToServer = async (item: Office.MailboxItem, userEmail: string, config: CompanyConfig, headers: string | null) => {
        try {
            if (DEBUG_MODE) {
                console.log("Headers in sendReportToServer:", headers);
            }

            // Extract PhishBait values from headers
            const phishBaitValues = await extractPhishBaitValues(headers || '');
            console.log("Extracted PhishBait values for report:", phishBaitValues);

            const data = {
                companyId: config.companyId,
                employeeId: userEmail,
                emailId: item.itemId || "",
                subject: item.subject || "",
                sender: item.sender?.emailAddress || "",
                campaignId: phishBaitValues?.campaignId || null,
                templateId: phishBaitValues?.templateId || null,
                userId: phishBaitValues?.userId || null
            };

            if (DEBUG_MODE) {
                console.log("Sending report data:", data);
            }

            const response = await fetch("https://baitandphish.com/API/phishReport.php", {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify(data)
            });

            if (!response.ok) {
                throw new Error("Server error");
            } else if (response.ok) {
                notyf.success("Great job! You correctly identified a simulated phishing email.");
            }

            const result = await response.json();
            if (!result.success) throw new Error(result.message || "Server returned error");
        } catch (error) {
            notyf.error(error);
            await handleEmailForwarding(item, userEmail, config, false);
        }
    }

    // Create new email with attachment
    const createNewEmailWithAttachment = async (
        item: Office.MailboxItem,
        toRecipients: string[],
        subject: string,
        body: string,
        headers: string | null,
        isNewOutlook: boolean = false
    ): Promise<Office.AsyncResult<any>> => {
        if (DEBUG_MODE) {
            console.log("Creating new email with attachment to:", toRecipients);
        }

        return new Promise((resolve, reject) => {
            try {
                // Create HTML body with headers if available
                const htmlBody = createHtmlBody(body, headers);

                const options: any = {
                    toRecipients: toRecipients,
                    subject: subject,
                    htmlBody: htmlBody
                };

                // Only add attachment if we have an itemId
                if (item.itemId) {
                    options.attachments = [{
                        type: Office.MailboxEnums.AttachmentType.Item,
                        name: "Original Email.msg",
                        itemId: item.itemId
                    }];
                }

                // Only add displayMode for Classic Outlook
                if (!isNewOutlook && Office.MailboxEnums && Office.MailboxEnums.DisplayMode) {
                    try {
                        options.displayMode = Office.MailboxEnums.DisplayMode.NewWindow;
                        if (DEBUG_MODE) {
                            console.log("Added NewWindow display mode for Classic Outlook");
                        }
                    } catch (error) {
                        console.warn("Could not set display mode:", error);
                    }
                }

                if (DEBUG_MODE) {
                    console.log("Displaying new message form with options:", options);
                }

                // Wrap the displayNewMessageForm call in a try-catch
                try {
                    window.Office.context.mailbox.displayNewMessageForm(options, (result) => {
                        if (result.status === Office.AsyncResultStatus.Succeeded) {
                            resolve(result);
                        } else {
                            // If we get a SecurityError, try without the attachment
                            if (result.error && result.error.name === "SecurityError" && options.attachments) {
                                if (DEBUG_MODE) {
                                    console.log("SecurityError encountered, retrying without attachment");
                                }
                                delete options.attachments;
                                window.Office.context.mailbox.displayNewMessageForm(options, (retryResult) => {
                                    if (retryResult.status === Office.AsyncResultStatus.Succeeded) {
                                        resolve(retryResult);
                                    } else {
                                        reject(new Error(`Failed to create email without attachment: ${retryResult.error.message}`));
                                    }
                                });
                            } else {
                                reject(new Error(`Failed to create new email: ${result.error.message}`));
                            }
                        }
                    });
                } catch (displayError: any) {
                    console.error("Error displaying new message form:", displayError);
                    // If we get a SecurityError, try without the attachment
                    if (displayError.name === "SecurityError" && options.attachments) {
                        if (DEBUG_MODE) {
                            console.log("SecurityError encountered, retrying without attachment");
                        }
                        delete options.attachments;
                        try {
                            window.Office.context.mailbox.displayNewMessageForm(options, (retryResult) => {
                                if (retryResult.status === Office.AsyncResultStatus.Succeeded) {
                                    resolve(retryResult);
                                } else {
                                    reject(new Error(`Failed to create email without attachment: ${retryResult.error.message}`));
                                }
                            });
                        } catch (retryError: any) {
                            reject(new Error(`Failed to create email: ${retryError.message}`));
                        }
                    } else {
                        reject(displayError);
                    }
                }
            } catch (error) {
                console.error("Error in createNewEmailWithAttachment:", error);
                reject(error);
            }
        });
    }

    const handleEmailForwarding = async (item: any, userEmail: string, config: CompanyConfig, isSimulation: boolean, isNewOutlook = false) => {
        try {
            const headers = await getEmailHeaders(item);
            console.log("Headers in handleEmailForwarding:", headers);

            // Get admin emails from config
            const adminEmails = config.OtherPhishingTargetEmails.split(',').map((email: string) => email.trim());

            if (isSimulation) {
                // For simulated phishing, send data to server
                await sendReportToServer(item, userEmail, config, headers);

                // Display success message
                notyf.success("Phishing simulation reported successfully");
            } else {
                // For real phishing, create and send email to admins
                const formattedSubject = config.OtherPhishingSubject.replace("{subject}", item.subject || "");
                const formattedBody = config.OtherPhishingBody
                    .replace("{userEmail}", userEmail)
                    .replace("{date}", new Date().toLocaleString())
                    .replace("{subject}", item.subject || "")
                    .replace("{sender}", item.sender?.emailAddress || "");

                // Create and send email to admins
                await createNewEmailWithAttachment(
                    item,
                    adminEmails,
                    formattedSubject,
                    formattedBody,
                    headers,
                    isNewOutlook
                );

                notyf.success("Phishing email reported to security team");
            }

            // Delete the email after processing
            try {
                const accessToken = localStorage.getItem("access_token");
                if (!accessToken) {
                    throw new Error("Access token is missing. Please sign in again.");
                }

                if (!item.itemId) {
                    throw new Error("No email selected to delete.");
                }

                // Convert the itemId to Graph-compatible format
                const graphMsgId = window.Office.context.mailbox.convertToRestId(item.itemId, Office.MailboxEnums.RestVersion.v2_0);

                // Delete the email using the service
                await deleteEmail(accessToken, graphMsgId, (error, data) => {
                    if (error) {
                        console.error("Error deleting email:", error);
                        notyf.error("Could not delete the email. Please delete it manually.");
                    } else if (data) {
                        console.log(`Email deleted from Outlook`);
                        notyf.success("Email moved to deleted items");
                    }
                });
            } catch (deleteError) {
                console.error("Error deleting email:", deleteError);
                notyf.error("Could not delete the email. Please delete it manually.");
            }

        } catch (error) {
            console.error("Error in handleEmailForwarding:", error);
            notyf.error("Could not process the phishing report. Please try again or contact your IT department.");
        }
    }

    // Function to get original email data
    const getOriginalEmailData = async (item: Office.MailboxItem): Promise<string> => {
        return new Promise((resolve, reject) => {
            try {
                // Get the email headers and content
                const emailData = {
                    subject: item.subject || '',
                    sender: item.sender?.emailAddress || '',
                    headers: '',
                    body: item.body?.content || ''
                };

                // Get headers if available
                if (typeof item.getAllInternetHeadersAsync === 'function') {
                    item.getAllInternetHeadersAsync((result) => {
                        if (result.status === Office.AsyncResultStatus.Succeeded) {
                            emailData.headers = result.value || '';
                            // Convert the complete email data to base64
                            const emailContent = JSON.stringify(emailData);
                            const base64Data = btoa(emailContent);
                            resolve(base64Data);
                        } else {
                            // If headers fail, still send the rest of the data
                            const emailContent = JSON.stringify(emailData);
                            const base64Data = btoa(emailContent);
                            resolve(base64Data);
                        }
                    });
                } else {
                    // If headers are not available, send the rest of the data
                    const emailContent = JSON.stringify(emailData);
                    const base64Data = btoa(emailContent);
                    resolve(base64Data);
                }
            } catch (error) {
                reject(error);
            }
        });
    }

    // Optimized HTML body creation
    function createHtmlBody(body: string, headers: string) {
        const sanitizedBody = body
            .replace(/&/g, '&amp;')
            .replace(/</g, '&lt;')
            .replace(/>/g, '&gt;')
            .replace(/"/g, '&quot;')
            .replace(/'/g, '&#039;');

        let htmlBody = `
        <div style="background-color: #fff4ce; border-left: 4px solid #ffd700; padding: 10px; margin-bottom: 20px;">
            <p style="margin: 0; font-weight: bold; color: #7a6400;">IMPORTANT: Please click send on this message</p>
            <p style="margin: 10px 0 0 0; color: #7a6400;">
                The correct emails to send phishing reports to have been automatically added for you. 
                The original email has also automatically been attached and we have included the email headers below for analysis. 
                Thank you.
            </p>
        </div>
        <div style="margin-bottom: 20px; white-space: pre-wrap; word-wrap: break-word;">
            ${sanitizedBody.replace(/\n/g, "<br>")}
        </div>
    `;

        if (headers) {
            const sanitizedHeaders = headers
                .replace(/&/g, '&amp;')
                .replace(/</g, '&lt;')
                .replace(/>/g, '&gt;')
                .replace(/"/g, '&quot;')
                .replace(/'/g, '&#039;');

            htmlBody += `
            <div style="background-color: #f5f5f5; border: 1px solid #ddd; padding: 10px; margin-top: 20px;">
                <p style="margin: 0 0 10px 0; font-weight: bold; color: #333;">Email Headers (For Analysis):</p>
                <pre style="margin: 0; white-space: pre-wrap; word-wrap: break-word; font-family: monospace; font-size: 12px; color: #666;">${sanitizedHeaders}</pre>
            </div>
        `;
        }

        return htmlBody;
    }

    return (
        <>
            <Box sx={{ backgroundColor: '#f5f5f5', padding: "8px", borderRadius: 2, maxWidth: 600 }}>
                <Card sx={{ paddingLeft: "2px", paddingRight: "8px" }}>
                    <CardContent>
                        <Typography sx={{ fontWeight: "bold", fontSize: "20px", padding: "6px 0" }} color="primary">
                            {UIText.phishing.title}
                        </Typography>
                        <Typography variant="body1" paragraph>{UIText.phishing.description}</Typography>
                        <Box sx={{ borderLeft: '4px solid #2196f3', pl: 2, my: 2, borderRadius: "4px" }}>
                            <Typography variant="body2">
                                <strong>{UIText.phishing.from}</strong> {item.from.emailAddress}
                            </Typography>
                            <Typography variant="body2">
                                <strong>{UIText.phishing.subject}</strong> {item.subject}
                            </Typography>
                            <Typography variant="body2">
                                <strong>{UIText.phishing.received}</strong> {formattedDateTime}
                            </Typography>
                        </Box>
                        <Typography variant="body1" paragraph>{UIText.phishing.report_email}</Typography>
                        <Button
                            variant="contained"
                            color="primary"
                            fullWidth
                            onClick={handleReportAsPhishing}
                            sx={{ textTransform: "capitalize" }}
                        >
                            {UIText.phishing.button}
                        </Button>
                    </CardContent>
                </Card>
            </Box>
        </>
    );
}

export default Phishing;