import msalInstance from "./msalInstance";
import { loginRequest } from "./authConfig";

export const signInWithMicrosoft = async () => {
  try {
    // Ensure MSAL is initialized before calling login
    await msalInstance.initialize();

    const response = await msalInstance.loginPopup(loginRequest);
    return response.account;
  } catch (error) {
    console.error("Login failed:", error);
    throw error;
  }
};

export const getAccessToken = async () => {
  try {
    // Ensure MSAL is initialized
    await msalInstance.initialize();

    const accounts = msalInstance.getAllAccounts();
    if (accounts.length === 0) throw new Error("No user account found");

    const response = await msalInstance.acquireTokenSilent({
      ...loginRequest,
      account: accounts[0],
    });

    return response.accessToken;
  } catch (error) {
    console.warn("Silent token acquisition failed, trying popup login", error);
    return await msalInstance.acquireTokenPopup(loginRequest).then((res) => res.accessToken);
  }
};
