import { Notyf } from "notyf";
import 'notyf/notyf.min.css';

export const notyf = new Notyf({
    duration: 3000,
    position: { x: 'right', y: 'bottom' },
    types: [
        {
            type: 'success',
            background: '#0D9D58',
        },
        {
            type: 'error',
            background: '#dc2626',
        },
    ],
});