import React, { useState, useEffect } from 'react';
import { Typography, Box, Button, Card, CardContent, CircularProgress, Tooltip } from '@mui/material';
import UIText from '../../../utilities/textResource';
import { notyf } from '../../../hooks/useToast';
import { deleteEmail } from '../../../services/deleteEmail';

const PhishingPage: React.FC = () => {
    const [loading, setLoading] = useState(true);
    const [deleting, setDeleting] = useState(false);

    useEffect(() => {
        const timer = setTimeout(() => {
            setLoading(false);
        }, 2000);

        return () => clearTimeout(timer);
    }, []);

    const handleDeleteCurrentMail = async () => {
        try {
            setDeleting(true);

            const accessToken = localStorage.getItem("access_token");
            const item = window.Office.context.mailbox.item;
            const msgId = item.itemId;

            if (!accessToken) {
                throw new Error("Access token is missing. Please sign in again.");
            }

            if (!msgId) {
                throw new Error("No email selected to delete.");
            }

            // Convert the itemId to Graph-compatible format
            const graphMsgId = window.Office.context.mailbox.convertToRestId(msgId, Office.MailboxEnums.RestVersion.v2_0);

            // Delete the email using the service
            await deleteEmail(accessToken, graphMsgId, (error, data) => {
                if (error) {
                    console.error("Error deleting email:", error);
                    notyf.error("Failed to delete email");
                } else if (data) {
                    console.log(`Email deleted from Outlook`);
                    notyf.success("Email successfully processed and deleted");
                }
            });
        } catch (error) {
            console.error("Error processing email:", error);
            notyf.error("Failed to process email");
        } finally {
            setDeleting(false);
        }
    };

    return (
        <>
            <Box sx={{ backgroundColor: '#f5f5f5', padding: "8px", borderRadius: 2, maxWidth: 600 }}>
                <Card sx={{ paddingLeft: "2px", paddingRight: "8px" }}>
                    <CardContent>
                        {loading ? (
                            <>
                                <Typography sx={{ fontWeight: "bold", fontSize: "18px", padding: "6px 0" }} color="primary">
                                    {UIText.phishing.success.loading.title}
                                </Typography>

                                <Box sx={{ display: 'flex', justifyContent: 'center', alignItems: 'center', minHeight: 120 }}>
                                    <CircularProgress sx={{ color: '#0288D1' }} size={30} />
                                </Box>

                                <Typography sx={{ padding: "6px 0" }}>
                                    {UIText.phishing.success.loading.description}
                                </Typography>
                            </>
                        ) : (
                            <>
                                <Typography sx={{ fontWeight: "bold", fontSize: "18px", padding: "6px 0" }} color="primary">
                                    {UIText.phishing.success.title}
                                </Typography>

                                <Box sx={{ background: '#d4f5d4', borderLeft: '6px solid #219653', borderRadius: 2, px: 2, py: 2, mb: 2, borderRight: "4px" }}>
                                    <Typography variant="body1" sx={{ color: '#218838' }}>
                                        {UIText.phishing.success.success_msg}
                                    </Typography>
                                </Box>
                                <Typography variant="body1" paragraph>{UIText.phishing.success.description}</Typography>
                                <Tooltip placement='top' title={'This action will delete the reported email from your inbox.'}>
                                    <Button
                                        variant="contained"
                                        color="primary"
                                        style={{ textTransform: "capitalize" }}
                                        onClick={handleDeleteCurrentMail}
                                        disabled={deleting}
                                    >
                                        {deleting ? UIText.phishing.success.buttons.deleting : UIText.phishing.success.buttons.done}
                                    </Button>
                                </Tooltip>
                            </>
                        )}
                    </CardContent>
                </Card>
            </Box>
        </>
    );
};

export default PhishingPage;