import React, { useState, useEffect } from 'react';
import { Stack, Typography, Button } from '@mui/material';
import UIText from '../../../utilities/textResource';
import Loader from '../../loader/Loader';
import { useNavigate } from 'react-router-dom';
import { loginWithMicrosoft } from '../../auth/login';
import { getAccessToken, signInWithMicrosoft } from '../../auth/test/authService';

const LoginPage: React.FC = () => {
    const navigate = useNavigate();
    const [isLoading, setIsLoading] = useState(false);
    const [user, setUser] = useState<any>(null);
    const [token, setToken] = useState<string | null>(null);

    const handleLoginWithMicrosoft = async () => {
        setIsLoading(true);

        try {
            const userInfo = await signInWithMicrosoft();
            setUser(userInfo);

            const accessToken = await getAccessToken();
            setToken(accessToken);
            localStorage.setItem("access_token", accessToken);
            console.log("Access Token:", accessToken);

            navigate("/phishing");
        } catch (error) {
            console.error("Login error:", error);
            setIsLoading(false);
        } finally {
            setIsLoading(false);
        }
    };

    useEffect(() => {
        // Check if user is already logged in
        const token = localStorage.getItem("access_token");
        if (token) {
            navigate("/phishing");
        }
    });

    return (
        <>
            {isLoading && <Loader />}
            <Stack
                id="bias-report-home"
                sx={{
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                    alignItems: "center",
                    marginTop: "12px",
                    gap: "6px",
                }}
            >
                <img src="/assets/logo-filled.png" alt="This is a Phish" style={{ maxWidth: "100px", height: "100px" }} />
                <Typography sx={{ textAlign: "center", paddingLeft: "8px", paddingRight: "8px" }}>
                    {UIText.auth.description}
                </Typography>
            </Stack>

            <Stack alignItems="center" justifyContent="center" mt={2}>
                <Button
                    onClick={handleLoginWithMicrosoft}
                    variant="contained"
                    sx={{
                        display: "flex",
                        alignItems: "center",
                        gap: 1,
                        backgroundColor: "white",
                        borderRadius: "6px",
                        color: "black",
                        boxShadow: 2,
                        textTransform: "none",
                        fontSize: "0.875rem",
                        fontWeight: 500,
                        px: 3,
                        py: 1,
                        "&:hover": {
                            backgroundColor: "gray.200",
                        },
                    }}
                >
                    <img src="/assets/microsoft.png" alt="Microsoft" height={16} width={16} />
                    <span>{UIText.auth.login_with_microsoft}</span>
                </Button>
            </Stack>
        </>
    );
};

export default LoginPage;